  justify-content: center;
  align-items: center;
  font-weight: 500;
}

.services-sub {
  height: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* ----------------------------PAGE ONE START----------------------- */

.services-page-one {
  height: auto;
