  justify-content: center;
  align-items: center;
  margin-bottom: 9rem;
}

.page-one-box {
  width: 100%;
  background-color: #0099dd;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 2.8rem;
}
.page-one-box-custom {
  width: 100%;
