  flex-direction: row;
  align-items: initial;
  justify-content: center;
}

.box-left {
  display: flex;
  flex-direction: column;
  width: 65%;
  margin-left: 3rem;
}

.box-one-title {
  float: left;
  width: 100%;
  height: 10rem;
  display: flex;
  align-items: center;
}

.services-header-title {
  font-size: 50px;
  font-weight: bold;
  font-family: "Lora", serif;
  color: white;
  margin-bottom: 0;
  margin-top: 3.5rem;
}

.box-one-paragraph {
  display: flex;
  align-items: center;
  height: 15rem;
  width: 100%;
}

.services-paragraph {
  font-size: 20px;
  color: white;
  text-align: justify;
}

.box-one-button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 15rem;
}

.services-button {
  height: 72px;
  width: 254px;
  background: transparent;
  color: white;
  border: 1.5px solid skyblue;

  border-radius: 3px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.box-right {
  width: 35%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.first-box-right {
  width: 45%;
  display: flex;
  justify-content: center;
}
.right-image-section {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.image-file {
  height: 100%;
  width: 100%;
}
.image-file-double {
  width: 100%;
}

