* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.pricing-main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 9rem;
}
.pricing-table-section {
  width: 100%;
  height: 80%;
}
.table-header {
  height: 5rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #bdbdbd;
}
.feature-section {
  width: 40%;
}
.table-title {
  font-family: "Lora", serif;
  color: #10375c;
  font-weight: bold;
  font-size: 25px;
}
.silver-section,
.gold-section,
.platinum-section {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.feature p,
.silver p,
.gold p,
.platinum p {
  margin-bottom: 0;
  margin: 7px;
}
.silver-section-ex,
.gold-section-ex,
.platinum-section-ex {
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.table-content1 {
  min-height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #bdbdbd;
}
.table-content {
  min-height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #bdbdbd;
}
.feature {
  width: 40%;
}
.silver,
.gold,
.platinum {
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.silver-ex,
.gold-ex,
.platinum-ex {
  width: 15%;
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  margin-bottom: 0;
}

.pricing-btn-section {
  height: 8rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.button-section {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.silver-button,
.gold-button,
.platinum-button {
  height: 100%;
  width: 32%;
}
.silver-button-ex,
.gold-button-ex,
.platinum-button-ex {
  height: 100%;
  width: 25%;
}
.button-header {
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.buttons {
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.button-content {
  font-size: 1.5rem;
  font-weight: bold;
}
.btn-content-span {
  font-size: 14px;
  font-weight: normal;
}
.pricing-btn-silver {
  height: 2.5rem;
  width: 8rem;
  border-radius: 20px;
  border: none;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  background-color: #dd9f00;
  color: white;
}
.pricing-btn-gold {
  height: 2.5rem;
  width: 8rem;
  border-radius: 20px;
  border: none;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  background-color: #0099dd;
  color: white;
}
.pricing-btn-platinum {
  height: 2.5rem;
  width: 8rem;
  border-radius: 20px;
  border: none;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  background-color: #20d071;
  color: white;
}

@media only screen and (max-width: 1000px) {
  .pricing-main {
    height: auto;
    margin-bottom: 3rem;
  }
  .pricing-table-section {
    height: 100%;
  }
  .table-header {
    height: auto;
  }
  .feature-section {
    width: 40%;
  }

  .table-title {
    font-size: 15px;
  }
  .silver-section,
  .gold-section,
  .platinum-section {
    width: 20%;
  }
  .table-content1 {
    height: auto;
  }
  .table-content {
    height: auto;
  }
  .feature {
    width: 40%;
  }
  .silver,
  .gold,
  .platinum {
    width: 20%;
  }
  .pricing-button-section {
    height: auto;
  }
  .button-section {
    width: 60%;
  }
  .button-content {
    font-size: 1rem;
  }
}

@media only screen and (max-width: 1000px) {
  .table-title {
    font-size: 15px;
  }
  .feature p,
  .silver p,
  .gold p,
  .platinum p {
    font-size: 14px;
    margin-bottom: 0;
    margin: 5px;
  }
  .silver-section-ex p,
  .gold-section-ex p,
  .platinum-section-ex p {
    font-size: 14px;
    margin-bottom: 0;
    margin: 5px;
  }
  .silver img,
  .gold img,
  .platinum img {
    height: 25px;
    width: 25px;
  }
  .button-content {
    font-size: 15px;
  }
  .pricing-btn-silver,
  .pricing-btn-gold,
  .pricing-btn-platinum {
    font-size: 15px;
  }
}
