* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.sticky {
  position: sticky;
  top: 0;
}
.sticky.scrolled {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,
    rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

nav {
  position: fixed;
  z-index: 99;
  width: 100%;
  background-color: white;
}
nav .wrapper {
  position: relative;
  max-width: 80%;
  width: 80%;
  height: 40px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
}
/* .wrapper .logo a {
  color: #f2f2f2;
  font-size: 30px;
  font-weight: 600;
  text-decoration: none;
} */
.wrapper .nav-links {
  display: inline-flex;
  margin-bottom: 0;
  width: 100%;
  padding-left: 0;
  justify-content: inherit;
}
.nav-links li {
  list-style: none;
  /* width: 100%; */
}
.nav-link-li {
  display: flex;
  align-items: center;
}
.nav-links li a {
  color: black;
  text-decoration: none;
  font-size: 12px;
  font-weight: bold;
  /* padding: 9px 15px; */
  /* border-radius: 5px; */
  transition: all 0.3s ease;
}
.nav-links li a:hover {
  background: #0099dd;
  color: white;
}
.nav-links .mobile-item {
  display: none;
}
.dropdown-menu {
  display: block !important;
}

.nav-links .drop-menu {
  position: absolute;
  background: #242526;
  width: 180px;
  line-height: 45px;
  top: 85px;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}
.nav-links li:hover .drop-menu,
.nav-links li:hover .mega-box {
  transition: all 0.3s ease;
  top: 40px;
  opacity: 1;
  visibility: visible;
}

.drop-menu li a {
  width: 100%;
  display: block;
  padding: 0 0 0 15px;
  font-weight: 400;
  border-radius: 0px;
}
.mega-box {
  position: absolute;
  left: 0;
  width: 100%;
  top: 85px;
  opacity: 0;
  visibility: hidden;
  transition-delay: 0.25s;
}
.mega-box .content {
  background: white;
  padding: 25px 20px;
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  /* box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15); */
  /* padding-left: 8rem; */
}
.mega-box .content .row {
  width: calc(25% - 30px);
  line-height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.content .row header {
  color: #0099dd;
  font-size: 16px;
  font-weight: 500;
}
.content .row .mega-links {
  margin-left: -30px;
  border-left: 1px solid rgba(255, 255, 255, 0.09);
  width: auto;
  max-width: none;
}
.row .mega-links li {
  padding: 0 20px;
}
.row .mega-links li a {
  padding: 0px;
  padding: 0 10px;
  color: black;
  font-size: 13px;
  display: block;
  cursor: pointer;
}
.row .mega-links li a:hover {
  color: #f2f2f2;
}
.wrapper .btn {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  display: none;
  box-shadow: none;
}
.wrapper .btn:hover {
  box-shadow: none;
}
.wrapper .close-btn {
  position: absolute;
  right: 0;
  top: 10px;
}

@media screen and (max-width: 970px) {
  .wrapper .btn {
    display: block;
  }
  nav .wrapper {
    max-width: 100%;
    width: 80%;
  }
  .wrapper .nav-links {
    position: fixed;
    height: 100vh;
    width: 100%;
    max-width: 350px;
    top: 0;
    left: -100%;
    background: #0099dd;
    display: block;
    padding: 50px 10px;
    /* line-height: 0; */
    overflow-y: auto;
    box-shadow: 0px 15px 15px rgba(0, 0, 0, 0.18);
    transition: all 0.3s ease;
  }
  /* custom scroll bar */
  ::-webkit-scrollbar {
    width: 10px;
  }
  ::-webkit-scrollbar-track {
    background: #242526;
  }
  ::-webkit-scrollbar-thumb {
    background: #3a3b3c;
  }
  #menu-btn:checked ~ .nav-links {
    left: 0%;
  }
  #menu-btn:checked ~ .btn.menu-btn {
    display: none;
  }
  #close-btn:checked ~ .btn.menu-btn {
    display: block;
  }
  .nav-link-li {
    flex-direction: column;
    align-items: flex-start;
  }
  .nav-links li {
    margin: 0px 10px;
  }
  .nav-links li a {
    padding: 0 20px;
    display: block;
    font-size: 20px;
  }
  .nav-links .drop-menu {
    position: static;
    opacity: 1;
    top: 65px;
    visibility: visible;
    padding-left: 20px;
    width: 100%;
    max-height: 0px;
    overflow: hidden;
    box-shadow: none;
    transition: all 0.3s ease;
  }

  #showMega1:checked ~ #megaBox-1,
  #showMega2:checked ~ #megaBox-2,
  #showMega3:checked ~ #megaBox-3,
  #showMega4:checked ~ #megaBox-4,
  #showMega5:checked ~ #megaBox-5 {
    max-height: 100%;
  }
  .nav-links .desktop-item {
    display: none;
  }
  .nav-links .mobile-item {
    display: block;
    color: #f2f2f2;
    font-size: 18px;
    font-weight: 500;
    padding-left: 20px;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.3s ease;
  }

  .nav-links .mobile-item:hover {
    background: #3a3b3c;
  }
  .drop-menu li {
    margin: 0;
  }
  .drop-menu li a {
    border-radius: 5px;
    font-size: 18px;
  }
  .mega-box {
    position: static;
    top: 65px;
    opacity: 1;
    visibility: visible;
    padding: 0 20px;
    max-height: 0px;
    overflow: hidden;
    transition: all 0.3s ease;
  }
  .mega-box .content {
    box-shadow: none;
    flex-direction: column;
    padding: 20px 20px 0 20px;
    background-color: #0099dd;
  }
  .mega-box .content .row {
    width: 100% !important;
    margin-bottom: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.08);
  }
  .mega-box .content .row:nth-child(1),
  .mega-box .content .row:nth-child(2) {
    border-top: 0px;
  }
  .content .row .mega-links {
    border-left: 0px;
    padding-left: 15px;
  }
  .row .mega-links li {
    margin: 0;
  }
  .content .row header {
    font-size: 19px;
    color: #ffd872;
  }
  .dropdown-menu {
    padding: 0;
    background-color: #0099dd !important;
    position: inherit !important;
    border: none !important;
    -webkit-box-shadow: none;
  }
}

@media screen and (max-width: 600px) {
  nav .wrapper {
    width: 90%;
  }
}

nav input {
  display: none;
}

.body-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  padding: 0 30px;
}
.body-text div {
  font-size: 45px;
  font-weight: 600;
}
.mega-box .content .row {
  width: calc(30% - 10px);
  line-height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

@media screen and (min-width: 1620px) and (max-width: 1920px) {
  nav .wrapper {
    max-width: 80%;
    width: 70%;
  }
  .nav-links li a {
    font-size: 15.5px;
  }
  .content .row header {
    font-size: 18px;
  }
  .mega-box .content .row {
    line-height: 45px;
  }
  .row .mega-links li a {
    font-size: 14px;
  }
}
@media screen and (min-width: 1367px) and (max-width: 1619px){
  .nav-links li a {
    font-size: 13.5px;
  }
  nav .wrapper {
    max-width: 80%;
    width: 70%;
  }
}
@media screen and (min-width: 971px) and (max-width: 1345px) {
  .nav-links li a {
    font-size: 10px;
  }
}
@media only screen and (min-width: 1921px) {
  nav .wrapper {
    width: 60%;
  }
}

@media only screen and (min-width: 2400px) {
  nav .wrapper {
    width: 40%;
  }
}
