* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.new-footer-main {
  height: 30vw;
  width: 100%;
  background-color: #065e85;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-sub {
  height: 83%;
  width: 83%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.footer-left {
  height: 100%;
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-logo-section {
  height: 20%;
  width: 100%;
}

.footer-logo {
  height: 60%;
  width: 20%;
  cursor: pointer;
}

.content-left-section {
  height: 80%;
  width: 100%;
  display: flex;
  /* flex-direction: row; */
  /* align-items: center; */
  justify-content: space-between;
}

.content-partition {
  height: 90%;
  width: 30%;
}

.footer-ul-left {
  height: 100%;
  width: 95%;
}

.footer-li-top {
  list-style: none;
  text-decoration: none;
  margin-bottom: 1rem;
  color: skyblue;
  font-family: "Lato", sans-serif;
  cursor: pointer;
}

.footer-li {
  list-style: none;
  text-decoration: none;
  color: #ffffff;
  margin-bottom: 10px;
  font-family: "Lato", sans-serif;
  cursor: pointer;
}

.footer-li a {
  color: #ffffff;
}
.footer-li a:hover {
  color: #ffd872;
  text-decoration: none;
}

.footer-right {
  height: 100%;
  width: 30%;
}

.footer-contact-section {
  height: 80%;
  width: 100%;
  border-radius: 50px 5px;
  background-color: #1176a3;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-ul-right {
  /* height: 70%;
  width: 77%; */
}
ol,
ul {
  padding-left: 0rem;
}

.social-media-section {
  /* height: 20%;
  width: 100%; */
}

.social-media {
  /* height: 1.1rem;
  width: 1.1rem; */
  margin: 10px;
  cursor: pointer;
}

@media only screen and (min-width: 601px) and (max-width: 1200px) {
  .new-footer-main {
    height: 70vh;
  }
  .footer-sub {
    height: 90%;
    width: 80%;
    flex-direction: column;
  }
  .footer-left {
    height: 60%;
    width: 95%;
  }
  .footer-li-top {
    font-size: 12px;
  }
  .footer-li {
    font-size: 12px;
    margin-bottom: 5px;
  }
  .footer-right {
    height: 35%;
    width: 50%;
  }

  .footer-contact-section {
    height: 90%;
    width: 100%;
  }
  .footer-ul-right {
    height: 80%;
    width: 80%;
  }
  .social-media-section {
    height: 10%;
    width: 100%;
  }
  .footer-logo {
    height: 2.5rem;
    width: 8rem;
  }
  .footer-contact-section .footer-ul-right .footer-li-top,
  .footer-contact-section .footer-ul-right .footer-li {
    font-size: 13px;
  }
}

@media only screen and (max-width: 600px) {
  .new-footer-main {
    height: auto;
  }
  .footer-sub {
    height: 85%;
    width: 90%;
    flex-direction: column;
  }

  .footer-left {
    height: 65%;
    width: 90%;
  }
  .footer-logo-section {
    /* height: 15%; */
    width: 90%;
  }
  .footer-logo {
    /* height: 2rem; */
    width: 7rem;
    margin-top: 1rem;
    /* margin-left: 7%; */
  }
  .content-left-section {
    height: 85%;
    width: 90%;
  }
  .content-partition {
    width: 32%;
    padding: 3px;
  }
  .footer-ul-left {
    width: 100%;
  }
  .footer-li-top {
    font-size: 12px;
    /* margin-bottom: 10px; */
  }
  .footer-li {
    font-size: 10px;
    /* margin-bottom: 7px; */
  }
  .footer-right {
    /* height: 30%; */
    width: 90%;
  }
  .footer-ul-right {
    /* margin-bottom: 0; */
  }
  .footer-contact-section {
    height: 90%;
    margin-top: 10px;
  }
  .footer-contact-section .footer-ul-right .footer-li-top,
  .footer-contact-section .footer-ul-right .footer-li {
    font-size: 10px;
    margin-bottom: 5px;
  }
  .social-media-section {
    height: 10%;
    display: flex;
    justify-content: center;
  }
  .social-media {
    height: 1rem;
    width: 1rem;
    margin: 5px;
    margin-top: 10px;
  }
  ol,
  ul {
    padding-left: 0rem;
    margin: 1rem 0;
  }
}
@media only screen and (min-width: 1930px) {
  .footer-sub {
    min-width: 40%;
    width: auto;
  }
}

@media only screen and (min-width: 1367px) and (max-width: 1920px) {
  .new-footer-main {
    height: 25vw;
  }
  .footer-sub {
    width: 70%;
  }
  .footer-contact-section .footer-ul-right .footer-li-top,
  .footer-contact-section .footer-ul-right .footer-li {
    font-size: 18px;
    margin-bottom: 10px;
  }
  .footer-li-top {
    font-size: 18px;
    margin-bottom: 18px;
  }
  .footer-li {
    font-size: 18px;
    margin-bottom: 14px;
  }
  .footer-logo {
    height: 3.3rem;
    width: 13rem;
  }
}
