* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
p {
  font-weight: 400;
}
.container {
  padding-left: 0;
  padding-right: 0;
}

.home-main-container {
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-first-section {
  height: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 7rem;
}

.first-section-header {
  max-width: 100%;
  padding-top: 0;
  margin-bottom: 10px;
}
.first-section-title {
  font-size: 2.55rem;
  text-align: justify;
  font-weight: bold;
  color: #10375c;
  font-family: "Lora", serif;
  margin-top: 2rem !important;
}

.first-section-content {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
}

.home-contact-form-section {
  width: 35%;
  background-color: #0099dd;
  position: relative;
  margin-right: -4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-contact-form {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.home-contact-title {
  font-size: 2rem;
  color: white;
  text-shadow: none;
  font-weight: bold;
  margin-bottom: 0;
  font-family: "Lora", serif;
  padding-top: 2rem;
  padding-bottom: 1rem;
}

.home-contact-input-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 3rem;
}

.home-contact-input {
  height: 2.5rem;
  margin: 7px;
  border-radius: 0;
  border: none;
  text-align: left;
  background-color: skyblue;
  width: 90%;
  text-indent: 5px;
  font-size: 1rem;
  font-weight: 450;
}

.home-contact-msg-input {
  height: 4rem;
  margin: 7px;
  border-radius: 0;
  text-align: left;
  background-color: skyblue;
  width: 90%;
  text-indent: 5px;
  font-size: 1rem;
  font-weight: 450;
  border: none;
}

.home-contact-button {
  height: 2.8rem;
  width: 90%;
  margin: 7px;
  border: none;
  background-color: #ffd872;
}

.first-section-image-container {
  width: 70%;
  display: flex;
  align-items: flex-end;
  height: 100%;
}

.first-section-image {
  height: 100%;
  width: 100%;
}

.overlay-image-section {
  position: absolute;
  right: 16rem;
  height: 730px;
}

.overlay-image {
  height: 100%;
}

.home-second-section {
  height: auto;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 9rem;
}

.second-section-header {
  /* height: 20%; */
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.second-section-content {
  display: flex;
  align-items: center;
}

.second-section-content-left {
  /* height: 80%; */
  width: 45%;
  margin-right: -4rem;
  position: relative;
  background-color: #0099dd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.second-section-content-p1 {
  height: 20%;
  width: 80%;
  font-size: 1rem;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 1rem;
  margin-top: 3rem;
}

.second-section-content-p2 {
  height: 25%;
  width: 80%;
  font-size: 2rem;
  line-height: 50px;
  font-weight: bold;
  color: white;
  font-family: "Lora", serif;
}

.second-section-content-p3 {
  height: 55%;
  width: 80%;
  color: white;
  font-size: 1rem;
  margin-bottom: 5rem;
}

.second-section-content-right {
  height: 95%;
  width: 60%;
}

.home-third-section {
  height: auto;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 9rem;
}

.third-section-content {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}

.third-section-content-left {
  /* height: 80%; */
  width: 45%;
  margin-left: -4rem;
  position: relative;
  background-color: #0099dd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.third-section-content-p1 {
  height: 20%;
  width: 80%;
  font-size: 1rem;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 1rem;
  margin-top: 3rem;
}

.third-section-content-p2 {
  height: 25%;
  width: 80%;
  font-size: 2rem;
  line-height: 50px;
  font-weight: bold;
  color: white;
  font-family: "Lora", serif;
}

.third-section-content-p3 {
  height: 55%;
  width: 80%;
  color: white;
  font-size: 1rem;
  margin-bottom: 5rem;
}

.third-section-content-right {
  width: 60%;
}

.home-fourth-section {
  height: auto;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 9rem;
}

@media only screen and (max-width: 850px) {
  .home-first-section {
    width: 90%;
    height: auto;
    margin-bottom: 3rem;
  }

  .first-section-header {
    padding: 0;
  }

  .first-section-title {
    margin-top: 1rem;
    font-size: 1.3rem;
    text-align: center;
  }

  .first-section-content {
    flex-direction: column-reverse;
  }

  .home-contact-form-section {
    width: 90%;
    margin: auto;
    margin-top: -3rem;
  }

  .home-contact-title {
    font-size: 1.15rem;
    padding-top: 1rem;
  }
  .home-contact-input-section {
    padding-bottom: 1rem;
  }

  .first-section-image-container {
    width: 100%;
    margin: 10px;
  }

  .overlay-image-section {
    display: none;
  }

  .home-second-section {
    width: 90%;
    margin-bottom: 3rem;
  }

  .second-section-content {
    flex-direction: column-reverse;
    /* max-width: 100%; */
  }

  .second-section-content-left {
    width: 90%;
    margin: auto;
    margin-top: -3rem;
  }

  .second-section-content-p1 {
    font-size: 12px;
    height: 15%;
    margin-top: 1rem;
  }

  .second-section-content-p2 {
    height: 25%;
    width: 80%;
    font-size: 1.1rem;
    margin-bottom: 5px;
    line-height: 30px;
  }

  .second-section-content-p3 {
    height: 60%;
    font-size: 12px;
    margin-bottom: 2rem;
  }

  .second-section-content-right {
    height: 50%;
    width: 100%;
  }

  .home-third-section {
    width: 90%;
    height: auto;
    margin-bottom: 3rem;
  }

  .third-section-content {
    flex-direction: column-reverse;
  }

  .third-section-content-left {
    width: 90%;
    margin: auto;
    margin-top: -3rem;
  }

  .third-section-content-p1 {
    font-size: 12px;
    height: 15%;
    margin-top: 1rem;
  }

  .third-section-content-p2 {
    height: 25%;
    width: 80%;
    font-size: 1.1rem;
    margin-bottom: 5px;
    line-height: 30px;
  }

  .third-section-content-p3 {
    height: 60%;
    font-size: 12px;
    margin-bottom: 2rem;
  }

  .third-section-content-right {
    width: 100%;
  }

  .home-fourth-section {
    width: 90%;
    margin-bottom: 3rem;
  }
}

@media only screen and (min-width: 851px) and (max-width: 1200px) {
  .first-section-title {
    font-size: 1.7rem;
    text-align: center;
  }
  .overlay-image-section {
    display: none;
  }
  .first-section-content {
    display: flex;
    flex-direction: column-reverse;
  }
  .home-contact-form-section {
    width: 70%;
    position: relative;
    margin-right: 0;
    margin-top: -4rem;
  }
  .first-section-image-container {
    width: 100%;
  }
  .second-section-content-left {
    margin-right: -3rem;
  }
  .second-section-content-p1 {
    font-size: 14px;
    height: 10%;
  }
  .second-section-content-p2 {
    font-size: 20px;
    height: 15%;
    line-height: 30px;
  }
  .second-section-content-p3 {
    height: 75%;
    font-size: 15px;
  }
  .third-section-content-left {
    width: 45%;
    margin-left: -3rem;
    position: relative;
  }
  .third-section-content-p1 {
    height: 10%;
    width: 80%;
    font-size: 14px;
  }
  .third-section-content-p2 {
    height: 15%;
    width: 80%;
    font-size: 18px;
    line-height: 30px;
  }
  .third-section-content-p3 {
    height: 75%;
    width: 80%;
    color: white;
    font-size: 1rem;
  }
}
@media only screen and (min-width: 1201px) and (max-width: 1366px) {
  .home-contact-title {
    font-size: 1.5rem;
  }
  .overlay-image-section {
    height: 610px;
    right: 8rem;
  }
}

@media only screen and (min-width: 1367px) and (max-width: 1920px) {
  .home-first-section {
    width: 70%;
  }
  .first-section-title {
    font-size: 3rem;
  }
  .home-contact-title {
    font-size: 1.9rem;
  }

  .home-contact-input {
    height: 3rem;
  }
  .home-contact-msg-input {
    height: 7rem;
  }
  .home-second-section {
    width: 70%;
  }
  .second-section-content-p1 {
    font-size: 1.2rem;
  }
  .second-section-content-p2 {
    font-size: 2.2rem;
    height: 20%;
  }
  .second-section-content-p3 {
    height: 60%;
    font-size: 1.2rem;
  }
  .home-third-section {
    width: 70%;
  }
  .third-section-content-p1 {
    font-size: 1.2rem;
  }
  .third-section-content-p2 {
    height: 20%;
    font-size: 2.2rem;
  }
  .third-section-content-p3 {
    font-size: 1.2rem;
  }
  .home-fourth-section {
    width: 70%;
  }
}
@media only screen and (min-width: 1930px) {
  .home-first-section,
  .home-fourth-section,
  .home-third-section,
  .home-second-section {
    width: 60%;
  }
  .overlay-image-section {
    display: none;
  }
  /* .first-section-image-container {
    width: auto;
  } */
}
@media only screen and (min-width: 2400px) {
  .home-first-section,
  .home-fourth-section,
  .home-third-section,
  .home-second-section {
    width: 40%;
  }
}
