.header-main-container {
  height: 7.5rem;
  background-color: white;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-sub {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 0;
  padding-right: 0;
}

.logo-section {
  height: 90%;
  width: 50%;
  display: flex;
  align-items: center;
}

.header-logo {
  height: 70%;
  width: 40%;
  cursor: pointer;
}

.sign-log-section {
  height: 90%;
  width: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.log-in-section {
  height: 90%;
  width: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-log-in {
  height: 2rem;
  width: 5rem;
  border: none;
  color: #0099dd;
  background-color: transparent;
}
.button-log-in:hover {
  color: #000000;
  text-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: 0.2s ease;
}

.sign-in-section {
  height: 90%;
  width: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-sign-in {
  height: 2.5rem;
  width: 6.5rem;
  border: none;
  color: white;
  background-color: #0099dd;
}
.button-sign-in:hover {
  color: #000000;
  background-color: #ffd872;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: 0.2s ease;
}

@media only screen and (min-width: 1367px) {
  .header-main-container {
    height: 7.5rem;
  }
  .header-logo {
    height: 90%;
  }
}

@media only screen and (max-width: 600px) {
  .header-main-container {
    height: 5rem;
  }
  .header-sub {
    max-width: 90%;
  }
  .header-logo {
    height: 35px;
    width: 120px;
  }

  .button-sign-in {
    width: 5rem;
    height: 2.2rem;
    font-size: 14px;
  }

  .button-log-in {
    width: 4rem;
  }
}

@media only screen and (min-width: 601px) and (max-width: 1200px) {
  .header-logo {
    height: 43px;
    width: 170px;
  }
}

@media only screen and (min-width: 1367px) {
  .header-sub {
    width: 70%;
  }
  .header-logo {
    height: 60px;
    width: 230px;
  }
}
@media only screen and (min-width: 1930px) {
  .header-sub {
    width: 60%;
  }
}
@media only screen and (min-width: 2400px) {
  .header-sub {
    width: 40%;
  }
}
